.canvas{
    width: 480px;
    height: 416px;
    border: 5px solid black;
    position: relative;
}

.W{
    background-image: url("./resources/wall/solid.png");
}

.block{
    width: 32px;
    height: 32px;
}
.row{
    display: flex;
}
.empty{
    background-image: url("./resources/empty/empty.png");
}
.P{
    background-image: url("./resources/player/player.gif");
    width: 32px;
    height: 64px;
    background-repeat: no-repeat;
    position: absolute;
}
.p-up{
    background-image: url("./resources/player/playerup.gif");
    width: 32px;
    height: 64px;
    background-repeat: no-repeat;
    position: absolute;
}
.p-left{
    background-image: url("./resources/player/player_left.gif");
    width: 32px;
    height: 64px;
    background-repeat: no-repeat;
    position: absolute;
}
.p-right{
    background-image: url("./resources/player/player_right.gif");
    width: 32px;
    height: 64px;
    background-repeat: no-repeat;
    position: absolute;
}
.G{
    background-image: url("./resources/goal/goal.png");
}
.Y{
    background-image: url("./resources/yellow_enemy/yellow_down.gif");
    width: 32px;
    height: 64px;
    background-repeat: no-repeat;
    position: absolute;
}
.y-left{
    background-image: url("./resources/yellow_enemy/yellow-left.gif");
    width: 32px;
    height: 64px;
    background-repeat: no-repeat;
    position: absolute;
}
.y-right{
    background-image: url("./resources/yellow_enemy/yellow-right.gif");
    width: 32px;
    height: 64px;
    background-repeat: no-repeat;
    position: absolute;
}
.y-up{
    background-image: url("./resources/yellow_enemy/yellow-up.gif");
    width: 32px;
    height: 64px;
    background-repeat: no-repeat;
    position: absolute;
}
.header{
    height: 64px;
    background-color: rgb(228,133,32);
}
.header-container{
    width: 240px;
    height:64px;
    margin: auto;
    display: flex;
    color: white;
    justify-content: space-between;
    align-items: center;
}
.B{
    background-image: url("./resources/broken/broken.png"); 
}
.bomb{
    background-image: url("./resources/bomb/bomb.gif");
    position: absolute;
    background-repeat: no-repeat;
    width: 32px;
    height: 32px;
    visibility: visible;
}
.explode-center{
    background-image: url("./resources/explosion/centre.png");
    width: 32px;
    height: 32px;
}
.horizontal{
    background-image: url("./resources/explosion/horizontal.png");
    position: absolute;
    width: 32px;
    height: 32px;
}
.vertical{
    background-image: url("./resources/explosion/vertical.png");
}
.game-over{
    background-color: rgb(228,133,32);
    height: 413px;
    border-top: 3px solid black;
    font-size: 40px;
    align-items: center;
    display: flex;
    justify-content: center;
    color: white;
}